import Vue from "vue";
import vuetify from "@/plugins/vuetify";

import App from "./App.vue";
import router from "./router";
import store from "./store";

Vue.config.productionTip = false;

new Vue({
  vuetify,
  router,
  store,
  mounted: function() {
    window._$shared = this.$store.state.ar; //to share with any ar iframe
  },
  render: h => h(App)
}).$mount("#app");
