export default {
  namespaced: true,
  state: {
    displayMode: "VR", // by default "VR" OR "AR",
    mediumPrecision: false,
    currentSFprefab: ""
  },
  mutations: {
    changeToARMode(state) {
      state.displayMode = "AR";
    },
    changeToVRMode(state) {
      state.displayMode = "VR";
    },
    mediumPrecision(state, value) {
      state.mediumPrecision = value;
    },
    setCurrentSFprefab(state, value) {
      state.currentSFprefab = value;
    }
  },
  getters: {
    isARMode(state) {
      return state.displayMode === "AR";
    }
  }
};
